<template>
  <div class="col-layout-wrapper">
    <div class="next-box" @click="next">next</div>
    <div class="wrapper-content" v-if="pageNum === 0">
      <div class="content-word">:,,</div>
      <div class="content-example">
        <div class="content-box pull-right">200px</div>
        <div class="content-box pull-left">200px</div>
        <div class="content-box"></div>
      </div>
    </div>
    <div class="wrapper-content" v-if="pageNum === 1">
      <el-scrollbar class="layout-new-box">
        <div class="inner-page">
          <div class="line-layout clearfix" :style="item.box4" :class="{'active': cssIndex === index}" v-for="(item, index) in cssList" :key="index" @click="reset(index)">
            <div class="line-item" :style="item.box1">box-1</div>
            <div class="line-item" :style="item.box2">box-2</div>
            <div class="line-item" :style="item.box3">box-3</div>
          </div>
        </div>
      </el-scrollbar>
      <div class="layout-css-box">
        <div></div>
        <textarea class="box-css-inner" v-model="cssInner.inner1"></textarea>
        <div></div>
        <textarea class="box-css-inner" v-model="cssInner.inner2"></textarea>
        <div></div>
        <textarea class="box-css-inner" v-model="cssInner.inner3"></textarea>
        <div></div>
        <textarea class="box-css-inner" v-model="cssInner.inner4"></textarea>
      </div>
      <button class="new-btn" @click="setBox"></button>
    </div>
    <div class="wrapper-content" v-if="pageNum === 2">
      <div class="content-disc-title">1.	</div>
      <p class="content-disc"><br>calc1px</p>
      <div class="content-disc-title">2.	</div>
      <p class="content-disc"><br></p>
      <div class="content-disc-title">3.	flex</div>
      <p class="content-disc"><br>ie8ie8</p>
      <div class="content-disc-title">4.	</div>
      <p class="content-disc"><br></p>
      <div class="content-disc-title">5.	bfc<span class="content-dis-hot">()</span></div>
      <p class="content-disc"><br></p>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
  },
  name: 'columnLayout',
  data() {
    return {
      cssList: [
        {
          box1: {},
          box2: {},
          box3: {}
        }
      ],
      cssIndex: 0,
      cssInner: {
        inner1: '',
        inner2: '',
        inner3: '',
        inner4: ''
      },
      pageNum: 0
    }
  },
  methods: {
    ...mapActions([
    ]),
    setBox() {
      let box1Css = {}
      let box2Css = {}
      let box3Css = {}
      let box4Css = {}
      if (this.cssInner.inner1.length > 0) {
        let box1 = this.cssInner.inner1.split(/\n/);
        box1.forEach(ele => {
          if (ele.length === 0) {
            return;
          }
          let value = ele.split(': ');
          box1Css[value[0]] = value[1].substring(0,value[1].length - 1);
        });
      }
      if (this.cssInner.inner2.length > 0) {
        let box2 = this.cssInner.inner2.split(/\n/);
        box2.forEach(ele => {
          if (ele.length === 0) {
            return;
          }
          let value = ele.split(': ');
          box2Css[value[0]] = value[1].substring(0,value[1].length - 1);
        })
      }
      if (this.cssInner.inner3.length > 0) {
        let box3 = this.cssInner.inner3.split(/\n/);
        box3.forEach(ele => {
          if (ele.length === 0) {
            return;
          }
          let value = ele.split(': ');
          box3Css[value[0]] = value[1].substring(0,value[1].length - 1);
        })
      }
      if (this.cssInner.inner4.length > 0) {
        let box4 = this.cssInner.inner4.split(/\n/);
        box4.forEach(ele => {
          if (ele.length === 0) {
            return;
          }
          let value = ele.split(': ');
          box4Css[value[0]] = value[1].substring(0,value[1].length - 1);
        })
      }
      if (this.cssIndex < this.cssList.length) {
        this.cssList[this.cssIndex] = {
            box1: box1Css,
            box2: box2Css,
            box3: box3Css,
            box4: box4Css,
        }
      } else {
        this.cssList.push({
          box1: box1Css,
          box2: box2Css,
          box3: box3Css,
          box4: box4Css
        })
      }
      this.cssIndex = this.cssList.length;
      this.cssInner = {
        inner1: '',
        inner2: '',
        inner3: '',
        inner4: ''
      }
    },
    reset(index) {
      this.cssIndex=index;
      let box1str = '';
      let box2str = '';
      let box3str = '';
      let box4str = '';
      for(let key in this.cssList[this.cssIndex].box1){
        let value = [];
        value.push(key);
        value.push(this.cssList[this.cssIndex].box1[key]);
        let str = value.join(': ');
        str += ';\n';
        box1str +=str;
      }
      for(let key in this.cssList[this.cssIndex].box2){
        let value = [];
        value.push(key);
        value.push(this.cssList[this.cssIndex].box2[key]);
        let str = value.join(': ');
        str += ';\n';
        box2str +=str;
      }
      for(let key in this.cssList[this.cssIndex].box3){
        let value = [];
        value.push(key);
        value.push(this.cssList[this.cssIndex].box3[key]);
        let str = value.join(': ');
        str += ';\n';
        box3str +=str;
      }
      for(let key in this.cssList[this.cssIndex].box4){
        let value = [];
        value.push(key);
        value.push(this.cssList[this.cssIndex].box4[key]);
        let str = value.join(': ');
        str += ';\n';
        box4str +=str;
      }
      this.cssInner = {
        inner1: box1str,
        inner2: box2str,
        inner3: box3str,
        inner4: box4str
      }
    },
    next() {
      if (this.pageNum < 2) {
        this.pageNum++;
      } else {
        this.$router.push('/#/');
      }
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
    this.pageNum = 0;
  },
  mounted() {
  }
};
</script>
<style>
#app .el-scrollbar__wrap {
  overflow-x: hidden;
}
</style>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.col-layout-wrapper {
  width: 100%;
  height: 100%;
  padding-top: 150px;
  .layout-new-box {
    position: relative;
    width: 80%;
    height: 50%;
    margin: 0 auto;
    border: 1px dashed #9454fc;
  }
  .inner-page {
    width: 100%;
    min-height: 400px;
    overflow-x: hidden;
  }
  .line-layout {
    min-height: 35px;
    margin-bottom: 10px;
    &.active .line-item{
      background-color: #9451ff;
    }
  }
  .line-item {
    color: #fff;
    text-align: center;
    font-size: 22px;
    line-height: 30px;
    border: 1px solid #fff;
    background-color: #7017ff;
    cursor: pointer;
  }
  .layout-css-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    padding-top: 10px;
    margin: 0 auto;
  }
  .box-css-inner {
    width: 15%;
    height: 300px;
    resize: none;
  }
  .new-btn {
    display: block;
    padding: 5px 20px;
    margin: 10px auto;
    border: none;
    border-radius: 5px;
    color: #fff;
    background-color: #9454fc;
    cursor: pointer;
    &:focus,
    &:active {
      outline: none;
    }
  }
  .content-word {
    padding-top: 100px;
    padding-bottom: 50px;
    font-size: 30px;
    text-align: center;
  }
  .content-example {
    width: 60%;
    height: 500px;
    margin: 0 auto;
    border: 1px solid #333;
  }
  .content-box {
    height: 100%;
    text-align: center;
    &:nth-of-type(1) {
      width: 200px;
    }
    &:nth-of-type(2) {
      width: 200px;
    }
    &:nth-of-type(3) {
      overflow: hidden;
      border-left: 1px dashed #000;
      border-right: 1px dashed #000;
    }
  }

  .wrapper-content:last-of-type {
    padding: 50px 200px;
    text-align: left;
    font-size: 22px;
  }
  .content-disc-title {
    font-size: 24px;
    &:hover {
      color: #00a4ff;
      text-shadow: 0 3px 10px rgb(70, 187, 255);
      &+.content-disc {
        color: #00a4ff;
      }
    }
  }
  .content-disc:hover {
    color: #00a4ff;
  }
  .content-disc-title .content-dis-hot {
    color: red;
    &:hover {
      text-shadow: 0 3px 10px rgb(255, 115, 115);
    }
  }
}
</style>