<template>
  <div class="stream-text-wrapper">
    <div class="menu-stream">
      <div :class="['menu-text', {'active': menuId === 0}]" @click="menuId=0"></div>
      <div :class="['menu-text', {'active': menuId === 1}]" @click="menuId=1"></div>
    </div>
    <div class="menu-content" v-if="menuId===0">
      <div class="indent-menus pull-left">
        <div class="indext-menu-item item-title"></div>
        <div class="indext-menu-item" v-for="(item, index) in indentSpace" :key="index" @click="addDisc(item.disc)">{{item.name}}</div>
        <div class="indext-menu-item item-title" @click="addDisc(cssDisc)">css</div>
      </div>
      <div class="indent-discribe" v-if="discPart.length > 1">
        <div class="indent-disc-content" v-for="(item, index) in discPart" :key="index" >{{item}}</div>
      </div>
      <div class="indent-discribe" v-else>
        <a class="indent-disc-content" target="blank" href="https://www.w3school.com.cn/tiy/t.asp?f=csse_text-indent">{{discPart[0]}}</a>
      </div>
    </div>
    <div class="menu-content" v-if="menuId===1">
      <div class="justify-content">
        <p class="justify-method" :style="methodCss"><span class="addCss" @click="addCss('text-align', 'justify')">text-align: justify;</span>,<span class="addCss" @click="addCss('text-align-last', 'justify')">text-align-lastjustify;</span>ie<span class="addCss" @click="addCss('text-justify', 'inter-ideograph')">text-justify:inter-ideograph;</span></p>
        <p class="justify-method" :style="methodCss">pctext-justify</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
  components: {
  },
  name: 'streamText',
  data() {
    return {
      menuId: -1,
      indentSpace: [
        {
          name:'&nbsp;',
          disc: ['&nbsp;', ',']
        },
        {
          name:'&ensp;',
          disc: ['&ensp;', '1/2']
        },
        {
          name:'&emsp;',
          disc: ['&emsp;', '1&emsp;']
        },
        {
          name:'&thinsp;',
          disc: ['','&thinsp;em','&zwnj;','&zwj;']
        },
        {
          name:'&zwnj;',
          disc: ['','&thinsp;em','&zwnj;','&zwj;']
        },
        {
          name:'&zwj;',
          disc: ['','&thinsp;em','&zwnj;','&zwj;']
        }
      ],
      discPart: [],
      cssDisc: ['text-indent: 2em;'],
      methodCss: []
    }
  },
  methods: {
    ...mapActions([
    ]),
    addDisc(disc) {
      this.discPart = disc;
    },
    addCss(name, value) {
      let obj = new Object();
      obj[name] = value;
      this.methodCss.push(obj)
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
  },
  mounted() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import '../assets/scss/common.scss';
.stream-text-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-top: 150px;
  .menu-stream {
    display: flex;
    width: 100%;
    height: 30px;
    justify-content: center;
  }
  .menu-text {
    padding-left: 20px;
    padding-right: 20px;
    font-size: 24px;
    line-height: 30px;
    color: #00a4ff;
    cursor: pointer;
    &.active {
      text-decoration: underline;
    }
  }
  .menu-content {
    flex: 1;
    width: 100%;
  }
  .indent-menus {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    width: 30%;
    height: 100%;
  }
  .indext-menu-item {
    width: 100%;
    height: 50px;
    text-align: right;
    line-height: 50px;
    font-size: 22px;
    cursor: pointer;
    &:first-of-type {
      cursor: default;
    }
    &.item-title {
      color: #003cff;
      font-size: 24px;
    }
    &:hover:not(.item-title) {
      color: #00a4ff;
    }
  }
  .indent-discribe {
    display: flex;
    align-content: center;
    flex-wrap: wrap;
    height: 100%;
    padding-left: 100px;
    padding-right: 300px;
  }
  .indent-disc-content {
    display: block;
    font-size: 24px;
    text-align: left;
    text-indent: 2em;
  }
  .justify-content {
    width: 100%;
    height: 100%;
    padding-left: 300px;
    padding-right: 300px;
    text-align: left;
    font-size: 22px;
  }
  .justify-method {
    text-indent: 2em;
  }
  .addCss {
    color: #00a4ff;
    cursor: pointer;
  }
}
</style>